	module.exports = {"data":{"post":{"html":"<p>PapaScott has been lately suffering from an overabundance of work (we found out the hard way that a <strike>universal</strike>uninterruptable power supply can be a single point of failure... I leave details to the imagination) and a shortage of inspiration. The situation will only become worse in the next couple of weeks, as Christopher's grandparents are coming for an extended visit, and Mama and I are dividing the hosting responsibilities (subtitle for this post: 'Separate Vacations'). I'm not sure whether the effect on the weblog will be noticeable (can a decrease in creativity be noticed when the amount of creativity was quite small to begin with?), but at least I'll be feeling less bad about it. Or at least more justified. Or whatever.</p>","fields":{"title":"You Can Dance For Inspiration","date":"10 Aug 2002","url":"https://papascott.de/2002-08-10-you-can-dance-for-inspiration/"}}},"pathContext":{"slug":"/2002-08-10-you-can-dance-for-inspiration/","prev":{"fields":{"title":"PHP. MySQL. Oh. Es. Ex.","date":"10 Aug 2002","slug":"/2002-08-10-php-mysql-oh-es-ex/"}},"next":{"fields":{"title":"Ich bin ein Slacker","date":"11 Aug 2002","slug":"/2002-08-11-ich-bin-ein-slacker/"}}}}

